<template>
  <div id="set">
    <div>
    <header>设置</header>
      <div class="wrapper" id="pwdWrap">
        <p class="title"><span class="title-icon"></span> 个人信息</p>
        <el-form ref="form" :model="modifyPwd" label-width="80px">
          <el-form-item label="用户名">
            <el-input v-model="modifyPwd.userName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="昵称">
            <el-input v-model="modifyPwd.nickName"></el-input>
            <span>1-20个字符</span>
          </el-form-item>
          <el-form-item label="原始密码">
            <el-input v-model="modifyPwd.originalPwd"></el-input>
          </el-form-item>
          <el-form-item label="新密码">
            <el-input v-model="modifyPwd.newPwd"></el-input>
          </el-form-item>
          <el-button type="primary" @click="saveUserInfo">保存</el-button>
        </el-form>
      </div>

      <div class="wrapper" id="sysWrap">
        <p class="title"><span class="title-icon"></span> 系统设置</p>
        <p class="desc-info">
          <span class="desc-text">面试开始前多长时间通知自己</span>
          <el-select v-model="setInfo.noticeTime"  class="small-select" placeholder="请选择">
            <el-option v-for="(item,index) in time" :key="index"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
          <span class="unit">分钟</span>
        </p>
        <p class="desc-info">
          <span class="desc-text">面试开始前多长时间二次提醒候选人</span>
          <el-select v-model="setInfo.secNoticeTime" class="small-select" placeholder="请选择">
            <el-option v-for="(item,index) in time" :key="index"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
          <span class="unit">分钟</span>
        </p>
        <p class="desc-info">
          <span class="desc-text">通知候选人时选择的通知方式</span>
          <el-select v-model="setInfo.noticeWay" class="large-select" placeholder="请选择">
            <el-option v-for="(item,index) in noticeWays" :key="index"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </p>
        <p class="desc-info">
          <span class="desc-text">通知时的模板内容</span>
          <span class="yellow fn-right">[  ] 内部分不需要手动输入，系统会自动抓取对应信息</span>
        </p>
        <!--<el-input id="templateWrapper" type="textarea"-->
          <!--:rows="2"-->
          <!--:autosize="{ minRows: 2, maxRows: 100}"-->
          <!--v-model="setInfo.template">-->
        <!--</el-input>-->
        <el-input id="templateWrapper" v-model="setInfo.template" ></el-input>
        <!--<div id="templateWrapper" v-model="setInfo.template">-->
        </div>
        <el-button type="primary" @click="saveSettingInfo">保存</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import  '../assets/css/set.less'
  import axios from 'axios';
  import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";

  export default {
    name:'Set',
    data() {
      return {
        modifyPwd:{
          userName:'',
          nickName:'',
          originalPwd:'',
          newPwd:''
        },
        setInfo:{
          noticeTime:'15',
          secNoticeTime:'15',
          noticeWay:'',
          template:'<p>亲爱的[姓名]： </p>\n' +
          '          <p class="indent">经过慎重的考虑，您非常符合我们公司的招聘要求，特邀请您参加<span class="yellow">[公司名称]</span> - <span class="yellow">[岗位]</span>的在线面试，请您务必准时参加！</p>\n' +
          '          <p class="indent"> 面试时间：<span class="yellow">[面试时间]</span></p>\n' +
          '          <p class="indent"> 面试间链接：<span class="yellow">[面试地址]</span></p>\n' +
          '          <p class="indent"> 面试邀请码：<span class="yellow">[面试邀请码]</span></p>'
        },
        noticeWays:[
          {
          value: 1,
          label: '仅邮件'
          },{
            value: 2,
            label: '仅短信'
          },{
            value: 3,
            label: '短信邮件同时'
          }
      ],
        time:[]
      }
    },
    methods: {
      timeList(){
        let _this=this;
        for(let i=1;i<25;i++){
          let time=5*i;
          _this.time.push(
            {
              value:time,
              label:time
            }
          )
        }
      },
      getUserInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/getuserinfo`).then((res) => {

            if (res.status !== 200) {
              return;
            }
          let resData=res.data;
            if(resData.returncode===0){
              let result=resData.result;
              _this.modifyPwd.userName=result.UserName;
              _this.modifyPwd.nickName=result.NickName;
            }
        })
      },
      getSettingInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/getsettinginfo`).then((res) => {
          if (res.status !== 200) {
            return;
          }
          let resData=res.data;
          if(resData.returncode===0){
            let result=resData.result;
            _this.setInfo.noticeTime=result.NotifyMeTime;
            _this.setInfo.secNoticeTime=result.SecondConfirmTime;
            _this.setInfo.noticeWay=result.NotifyType;
            _this.setInfo.template=result.NotifyTemplete;
          }
        })
      },
      saveUserInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/saveuserinfo`,{
          param:{
            username: _this.setInfo.userName,
            nickname: _this.setInfo.nickName,
            password: _this.setInfo.originalPwd,
            newpwd: _this.setInfo.newPwd
          }
        }).then((res) => {
          console.log(res);
          debugger;
          if (res.status !== 200) {
            return;
          }
          let resData=res.data;
          if(resData.returncode===0){

          }
        })
      },
      saveSettingInfo(){
        let _this=this;
        axios.get(`http://videoonline.zhaopin.com/setting/savesettinginfo`,{
          param:{
            NotifyMeTime: _this.modifyPwd.noticeTime,
            SecondConfirmTime: _this.modifyPwd.secNoticeTime,
            NotifyType: _this.modifyPwd.noticeWay,
            NotifyTemplete: _this.modifyPwd.template
          }
        }).then((res) => {
          if (res.status !== 200) {
            return;
          }
          let resData=res.data;
          if(resData.returncode===0){

          }
        })
      }
    },
    created(){
      this.timeList();
      this.getUserInfo();
      this.getSettingInfo();
    },
    components: {
      ElButton
//      'navigation': Navigation
    }
  }
</script>



